  componentDidMount() {
    if (!localStorage.getItem('logInUserData')) {
      let container = document.querySelector('.container')
      let button = document.querySelector('.button')
      let buttonSide = document.querySelectorAll('.button_side')
      let buttonGlow = document.querySelector('.button_glow')
      console.log('container', container)

      let rotationForce = 0.01

      let buttonPos = {
        x: container ? container.offsetLeft + button.offsetWidth / 2 : 0,
        y: container ? container.offsetTop + button.offsetHeight / 2 : 0
      }

      let mouse = {
        x: 0,
        y: 0
      }

      let rotation = {
        x: 0,
        y: 0
      }

      window.addEventListener('mousemove', function (event) {
        if (this.window.screen.width > 600) {
          mouse.x = event.clientX
          mouse.y = event.clientY

          rotation.x =
            (mouse.y - buttonPos.y) / (window.innerHeight * rotationForce)
          rotation.y =
            (mouse.x - buttonPos.x) / (window.innerWidth * rotationForce)

          button.style.transform =
            'rotateX(' +
            -1 * rotation.x +
            'deg)' +
            'rotateY(' +
            rotation.y +
            'deg)'
          buttonGlow.style.transform =
            'rotateX(' +
            rotation.x +
            'deg)' +
            'rotateY(' +
            -1 * rotation.y +
            'deg)'
          //buttonGlow.style.transform = 'rotateX(' + -1 * rotation.x + 'deg)' + 'rotateY(' + -1 * rotation.y + 'deg)';
        }
      })
    }
  }

